import beritaRunningText from "@/components/sekolah/beritaRunningText.vue"
import beritaCard2 from "@/components/Web/beritaCard2.vue";
export default{
  layout:"awal",
  mounted() {
      let fd = new FormData()
    fd.append('data', `select * from sekolah_berita order by id_berita desc`)//database setting di server
    fd.append('database','infolay3_test')
    axios.post('https://infolayanans.space/api/mysql/auto.php', fd
    , {
        headers: {
        Authorization: localStorage.getItem('auth._token.local')
        }
    }).then(res => {
        console.log(res)
        this.beritas =res.data
        this.$forceUpdate()
    })
    let fd12 = new FormData()
    fd12.append('data', `select * from sekolah_photo order by id_photo desc`)//database setting di server
    fd12.append('database','infolay3_test')
    axios.post('https://infolayanans.space/api/mysql/auto.php', fd12
    , {
        headers: {
        Authorization: localStorage.getItem('auth._token.local')
        }
    }).then(res => {
        console.log(res)
        this.photos =res.data
        this.$forceUpdate()
    })
    let fd5 = new FormData()
    fd5.append('data', `select * from sekolah_pengumuman`)//database setting di server
    fd5.append('database','infolay3_test')
    axios.post('https://infolayanans.space/api/mysql/auto.php', fd5
    , {
