<script>
import axios from 'axios'
var id = require("date-fns/locale/id");
export default {
  data() {
    return {
      beritas:[],
      // ganti navigasi untuk mengganti page
      navigasi: 0,
      info:{},
      // posisi page berubah
      posisi: 0
    };
  },
  props: {
    beritas: {
      type: Array,
      required: true,
      default: () => ["_id"]
    }
  },
  mounted() {
  
  },
  computed: {
    beritanya() {
      let data = this.beritas;
      console.log(data);
      data = this.$_.orderBy(data, ["viewed"], ["desc"]);
      console.log(data);
      return data;
    }
  },
  mounted() {
      let fd2 = new FormData()
    fd2.append('data', `select * from sekolah`)//database setting di server
    fd2.append('database','infolay3_test')
    axios.post('https://infolayanans.space/api/mysql/auto.php', fd2
    , {
        headers: {
        Authorization: localStorage.getItem('auth._token.local')
        }
    }).then(res => {
        console.log(res)
        this.info =res.data[0]
        this.$forceUpdate()
    })
  },
  methods: {
    change(item) {
      this.posisi = item;
    },
    truncate(value) {
      if (value.length > 20) {
        value = value.substring(0, 100) + " ...";
      }
      return value;
    },
     truncate2(value) {
      if (value.length > 20) {
        value = value.substring(0, 58) + " ...";
      }
      return value;
    },
    format() {
      return this.$datefns.format(new Date(2014, 6, 2), "dddd MMMM YYYY", {
        locale: id
      });
    }
  },
  filters: {
    //  limit text 20 character panjangnya
    
  }
};
</script>
